:root {
  --mdb-font-roboto: "Roboto", sans-serif;
  --mdb-bg-opacity: 1;
}

body {
  font-family: var(--mdb-font-roboto);
  line-height: 1.6;
  color: #4f4f4f;
}

a {
  text-decoration: none;
}

button:focus {
  outline: 0;
}

.note {
  padding: 10px;
  border-left: 6px solid;
  border-radius: 5px;
}
.note strong {
  font-weight: 600;
}
.note p {
  font-weight: 500;
}

.note-primary {
  background-color: rgb(225.06812749, 235.6768924303, 253.23187251);
  border-color: #1266f1;
}

.note-secondary {
  background-color: rgb(243.7822335025, 226.5913705584, 254.7086294416);
  border-color: #b23cfd;
}

.note-success {
  background-color: rgb(198.3, 255, 221.2278688525);
  border-color: #00b74a;
}

.note-danger {
  background-color: rgb(254.1962264151, 227.4037735849, 232.0924528302);
  border-color: #f93154;
}

.note-warning {
  background-color: rgb(255, 241.24, 214.2);
  border-color: #ffa900;
}

.note-info {
  background-color: rgb(225.3, 245.55, 252.3);
  border-color: #39c0ed;
}

.note-light {
  background-color: #fbfbfb;
  border-color: #262626;
}

@media (min-width: 1199px) {
  .w-responsive {
    width: 75%;
  }
}

.bg-primary {
  background-color: rgba(18, 102, 241, var(--mdb-bg-opacity)) !important;
}

.bg-secondary {
  background-color: rgba(178, 60, 253, var(--mdb-bg-opacity)) !important;
}

.bg-success {
  background-color: rgba(0, 183, 74, var(--mdb-bg-opacity)) !important;
}

.bg-info {
