}

.bg-warning {
  background-color: rgba(255, 169, 0, var(--mdb-bg-opacity)) !important;
}

.bg-danger {
  background-color: rgba(249, 49, 84, var(--mdb-bg-opacity)) !important;
}

.bg-light {
