/*******************************
         Site Overrides
*******************************/
.bg-image {
  position: relative;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-attachment: fixed;
}

.hover-overlay .mask {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.hover-overlay .mask:hover {
  opacity: 1;
}

.hover-zoom img,
.hover-zoom video {
  transition: all 0.3s linear;
}
.hover-zoom:hover img,
.hover-zoom:hover video {
  transform: scale(1.1);
}

.hover-shadow,
.card.hover-shadow {
  box-shadow: none;
  transition: all 0.3s ease-in-out;
}
.hover-shadow:hover,
.card.hover-shadow:hover {
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.21);
  transition: all 0.3s ease-in-out;
}

.bg-fixed {
  background-attachment: fixed;
}

.hover-shadow-soft,
.card.hover-shadow-soft {
  box-shadow: none;
  transition: all 0.3s ease-in-out;
}
.hover-shadow-soft:hover,
.card.hover-shadow-soft:hover {
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.07), 0 10px 10px -5px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease-in-out;
}

.form-control {
  min-height: auto;
  padding-top: 4px;
  padding-bottom: 3.28px;
  transition: all 0.1s linear;
}
.form-control:focus {
  box-shadow: none;
  transition: all 0.1s linear;
  border-color: #1266f1;
  box-shadow: inset 0px 0px 0px 1px #1266f1;
}
.form-control.form-control-sm {
  font-size: 0.775rem;
  line-height: 1.5;
}
.form-control.form-control-lg {
  line-height: 2.15;
  border-radius: 0.25rem;
}

.form-outline {
  position: relative;
}
.form-outline .form-helper {
  width: 100%;
  position: absolute;
  font-size: 0.875em;
  color: #757575;
}
.form-outline .form-helper .form-counter {
  text-align: right;
}
.form-outline .trailing {
  position: absolute;
  right: 10px;
  left: initial;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}
.form-outline .form-icon-trailing {
  padding-right: 2rem !important;
}
.form-outline .form-control {
  min-height: auto;
  padding-top: 0.33em;
  padding-bottom: 0.33em;
  padding-left: 0.75em;
  padding-right: 0.75em;
  border: 0;
  background: transparent;
  transition: all 0.2s linear;
}
.form-outline .form-control ~ .form-label {
  position: absolute;
  top: 0;
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  left: 0.75rem;
  padding-top: 0.37rem;
  pointer-events: none;
  transform-origin: 0 0;
  transition: all 0.2s ease-out;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
.form-outline .form-control ~ .form-notch {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  max-width: 100%;
  height: 100%;
  text-align: left;
  pointer-events: none;
}
.form-outline .form-control ~ .form-notch div {
  pointer-events: none;
  border: 1px solid;
  border-color: #bdbdbd;
  box-sizing: border-box;
  background: transparent;
}
.form-outline .form-control ~ .form-notch .form-notch-leading {
  left: 0;
  top: 0;
  height: 100%;
  width: 0.5rem;
  border-right: none;
  border-radius: 0.25rem 0 0 0.25rem;
}
.form-outline .form-control ~ .form-notch .form-notch-middle {
  flex: 0 0 auto;
  width: auto;
  max-width: calc(100% - 1rem);
  height: 100%;
  border-right: none;
  border-left: none;
}
.form-outline .form-control ~ .form-notch .form-notch-trailing {
  flex-grow: 1;
  height: 100%;
  border-left: none;
  border-radius: 0 0.25rem 0.25rem 0;
}
.form-outline .form-control:not(.placeholder-active)::placeholder {
  opacity: 0;
}
.form-outline .form-control:focus::placeholder, .form-outline .form-control.active::placeholder {
  opacity: 1;
}
.form-outline .form-control:focus {
  box-shadow: none !important;
}
.form-outline .form-control:focus ~ .form-label, .form-outline .form-control.active ~ .form-label {
  transform: translateY(-1rem) translateY(0.1rem) scale(0.8);
