  .carousel-indicators li {
    transition: none;
  }
}

.carousel-dark .carousel-indicators li {
  background-color: #000;
}

.dropdown-menu {
  color: #212529;
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
